import {useState} from 'react';
import Layout from '../../components/Layout';
import Sidebar from './sidebar';
import { useNavigate } from 'react-router-dom';
import CartsModal from './merchant_group/modals/carts.modal';

export const categories = [
  {
    name: 'POS',
    link: '/pos'
  }, 
  {
    name: 'SAVED CARTS',
    link: '/carts'
  },
  {
    name: 'ORDERS',
    link: '/orders'
  },
  {
    name: 'REPORT',
    link: '/report'
  },
  {
    name: 'SETTINGS',
    link: '/settings'
  },
  {
    name: 'INVENTORY',
    link: '/inventory'
  }
];

const Dashboard_Merchant = () => {
  const navigate = useNavigate();
  const [cartsModal, setCartsModal] = useState(false);

  const handleLink = (link) => {
    if (link === '/carts') {
      setCartsModal(true);
      return;
    }

    navigate(link);
  }

  return (
    <Layout classes={'bg-[#404040] h-full'}>
        <div className='flex flex-row'>
            <div className="w-full h-[calc(100vh-80px)]">
              <div className="grid grid-cols-3 gap-6 px-[2rem] py-[2rem]">
                  {
                      categories.map((cate, index) => {
                          return (
                              <div key={index} onClick={() => handleLink(cate.link)} className='w-full h-[6rem] text-xl text-center leading-[6rem] rounded-[0.6rem] bg-[#D9D9D9] text-gray-800 select-none cursor-pointer'>
                                  {cate.name}
                              </div>
                          )
                      })
                  }
              </div>
            </div>
            <Sidebar />
        </div>
        <CartsModal isModal={cartsModal} setIsModal={setCartsModal} cartsLink={'/carts'}/>
    </Layout>
  );
}

export default Dashboard_Merchant;